@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-calaveritas {
    display: grid;
    gap: 2rem;

    @include m.telefono{
        grid-template-columns: repeat(2, 1fr);
    }
    
    @include m.desktop{
        grid-template-columns: repeat(3, 1fr);
    }

    .calaverita {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 2rem;
        background-color: v.$naranja;
        color: v.$blanco;
        font-weight: 700;
        padding: 2rem;
        font-size: 2.2rem;

        .autor {
            color: v.$grisOscuro;
            font-size: 1.8rem;
        }
    }
}

.modal {
    .contenido-modal {
        max-height: 70vh;
        overflow: auto;
    }

    .calaverita {
        color: v.$blanco;
        font-size: 2.2rem;
        font-weight: 700;
    }
}

button.publico {
    @include m.boton(v.$azul, v.$blanco);
}

button.privado {
    @include m.boton(v.$morado, v.$blanco);
}