@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-productos {
    display: grid;
    gap: 2rem;

    @include m.telefono{
        grid-template-columns: repeat(2, 1fr);
    }

    @include m.desktop{
        grid-template-columns: repeat(3, 1fr);
    }

    .producto {
        display: grid;
        background-color: lighten(v.$naranja, 10%);
        padding: 1rem;
        gap: 1rem;

        img {
            width: 100%;
            height: auto;
        }

        span {
            color: v.$blanco;
            font-weight: 700;
            font-size: 2rem;

            &.precio {
                color: v.$morado;
            }
        }
    }
}