@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenido-seccion {
    padding: 2rem;
    background-color: lighten($color: v.$verde, $amount: 20%);
    display: grid;
    gap: 2rem;

    @include m.desktop{
        grid-template-columns: 1fr 2fr;

        .imagen {
            grid-column: 1/2;
            grid-row: 1/2;
        }

        .caracteristicas-seccion {
            grid-column: 2/3;
            grid-row: 1/2;
        }

        .contenido {
            grid-column: 1/3;
        }

    }


    .caracteristicas-seccion {
        li {
            font-size: 2.2rem;
            color: v.$blanco;
            font-weight: 700;
        }
    }

    .contenido {
        color: v.$grisOscuro;

        blockquote {
            font-size: 1.8rem;
        }
    }
}