@use 'base/variables' as v;

@mixin telefono{
    @media(min-width: 480px){
        @content;
    }
}

@mixin tablet{
    @media(min-width: 768px){
        @content;
    }
}

@mixin desktop{
    @media(min-width: 992px){
        @content;
    }
}

@mixin boton ($background, $color){
    display: block;
    width: 100%;
    border: none;
    border-radius: .5rem;
    color: $color;
    background-color: $background;
    padding: 1rem 3rem;
    margin: 2rem 0;
    font-size: 1.8rem;
    font-weight: 500;
    text-align: center;
    transition: background-color 500ms ease;

    @include tablet{
        display: inline-block;
        width: auto;
    }

    &:hover {
        background-color: darken($color: $background, $amount: 10%);
        cursor: pointer;
    }
}