@use 'base/variables' as v;

.contenedor {
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

.contenedor-sm {
    max-width: 70rem;
    width: 95%;
    margin: 0 auto;
}

.descripcion-pagina {
    color: v.$gris;
    text-align: center;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: 700;
}

.nombre-pagina {
    color: v.$grisOscuro;
    font-weight: 900;
    text-align: center;
    margin: 4rem 0 3rem 0;
}