@use 'base/mixins' as m;
@use 'base/variables' as v;

.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba($color: v.$negro, $alpha: 60%);

    .cerrar-modal {
        cursor: pointer;
        padding: .5rem 1rem;
        font-size: 3rem;
        color: v.$blanco;
        font-weight: 900;
        background-color: v.$rojo;
        display: inline-block;
        border-radius: .5rem;
        position: absolute;
        top: 3rem;
        right: 3rem;
    }

    .contenido-modal {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        background-color: v.$grisOscuro;
        padding: 2rem;
    }
}