html {
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
  font-size: 62.5%;
}

body {
  font-size: 16px;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.6rem;
}

h1,
h2,
h3 {
  margin: 0;
}

textarea {
  resize: none;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  width: 95%;
  margin: 0 auto;
}

.descripcion-pagina {
  color: rgb(119, 119, 119);
  text-align: center;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}

.nombre-pagina {
  color: rgb(75, 75, 75);
  font-weight: 900;
  text-align: center;
  margin: 4rem 0 3rem 0;
}

.alerta {
  padding: 1rem 1rem 1rem 3rem;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}
.alerta.exito {
  border-bottom: 0.3rem solid rgb(59, 179, 63);
  color: rgb(59, 179, 63);
  background-color: #cceecd;
}
.alerta.error {
  border-bottom: 0.3rem solid rgb(226, 66, 66);
  color: rgb(226, 66, 66);
  background-color: #fdf3f3;
}

.titulo {
  margin-top: 5rem;
  margin-bottom: 3rem;
  font-weight: 900;
}
@media (min-width: 768px) {
  .titulo {
    font-size: 4rem;
  }
}

.login .titulo {
  background: linear-gradient(45deg, rgb(114, 32, 245), #cfb2fc);
  color: transparent;
  background-clip: text;
}
.login .formulario label {
  color: rgb(114, 32, 245);
}
.login .formulario input[type=submit] {
  display: block;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  color: #fff;
  background-color: rgb(114, 32, 245);
  padding: 1rem 3rem;
  margin: 2rem 0;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  transition: background-color 500ms ease;
}
@media (min-width: 768px) {
  .login .formulario input[type=submit] {
    display: inline-block;
    width: auto;
  }
}
.login .formulario input[type=submit]:hover {
  background-color: #590ad8;
  cursor: pointer;
}

.formulario {
  max-width: 60rem;
  margin: 0 auto;
}
.formulario .campo {
  display: grid;
  gap: 1rem;
  margin-bottom: 2rem;
}
.formulario .campo:last-of-type {
  margin-bottom: 0;
}
.formulario .campo label {
  display: block;
  font-size: 2rem;
  font-weight: 700;
}
.formulario .campo input,
.formulario .campo textarea {
  width: 100%;
  margin: 0 auto;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: rgb(173, 173, 173) solid 1px;
}
.formulario .imagen {
  max-width: 20rem;
}

.crear.formulario label {
  color: rgb(59, 179, 63);
}
.crear.formulario input[type=file],
.crear.formulario input[type=submit] {
  display: block;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  color: #fff;
  background-color: rgb(59, 179, 63);
  padding: 1rem 3rem;
  margin: 2rem 0;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  transition: background-color 500ms ease;
}
@media (min-width: 768px) {
  .crear.formulario input[type=file],
  .crear.formulario input[type=submit] {
    display: inline-block;
    width: auto;
  }
}
.crear.formulario input[type=file]:hover,
.crear.formulario input[type=submit]:hover {
  background-color: #2e8d32;
  cursor: pointer;
}

.actualizar.formulario label {
  color: rgb(255, 134, 28);
}
.actualizar.formulario input[type=file],
.actualizar.formulario input[type=submit] {
  display: block;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  color: #fff;
  background-color: rgb(255, 134, 28);
  padding: 1rem 3rem;
  margin: 2rem 0;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  transition: background-color 500ms ease;
}
@media (min-width: 768px) {
  .actualizar.formulario input[type=file],
  .actualizar.formulario input[type=submit] {
    display: inline-block;
    width: auto;
  }
}
.actualizar.formulario input[type=file]:hover,
.actualizar.formulario input[type=submit]:hover {
  background-color: #e86c00;
  cursor: pointer;
}

@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
  .dashboard .sidebar {
    flex: 0 0 30rem;
  }
  .dashboard .contenedor-principal {
    flex: 1;
    overflow: auto;
  }
}

.contenedor-principal {
  margin-bottom: 3rem;
}
.contenedor-principal .barra {
  padding: 1rem;
  box-shadow: 0px 5px 5px 0px rgb(75, 75, 75);
}
@media (min-width: 768px) {
  .contenedor-principal .barra {
    display: flex;
    justify-content: space-between;
  }
}
.contenedor-principal .barra p {
  margin: 0;
  color: rgb(75, 75, 75);
  font-weight: 700;
  font-size: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .contenedor-principal .barra p {
    margin-bottom: 0;
  }
}
.contenedor-principal .barra p span {
  color: rgb(54, 137, 233);
}
.contenedor-principal .barra .cerrar-sesion {
  display: block;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  color: #fff;
  background-color: rgb(54, 137, 233);
  padding: 1rem 3rem;
  margin: 2rem 0;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
