.opciones-panel {
  display: grid;
  gap: 1rem;
}
@media (min-width: 768px) {
  .opciones-panel {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .opciones-panel {
    grid-template-columns: repeat(3, 1fr);
  }
}
