@media (min-width: 480px) {
  .contenedor-nosotros {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .contenedor-nosotros {
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor-nosotros .seccion {
  padding: 2rem;
  background-color: #cfb2fc;
  display: grid;
  gap: 2rem;
}
.contenedor-nosotros .seccion span {
  font-size: 2.2rem;
  font-weight: 700;
  color: rgb(75, 75, 75);
}

.contenido-seccion {
  padding: 2rem;
  background-color: #7fd582;
  display: grid;
  gap: 2rem;
}
@media (min-width: 992px) {
  .contenido-seccion {
    grid-template-columns: 1fr 2fr;
  }
  .contenido-seccion .imagen {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .contenido-seccion .caracteristicas-seccion {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .contenido-seccion .contenido {
    grid-column: 1/3;
  }
}
.contenido-seccion .caracteristicas-seccion li {
  font-size: 2.2rem;
  color: #fff;
  font-weight: 700;
}
.contenido-seccion .contenido {
  color: rgb(75, 75, 75);
}
.contenido-seccion .contenido blockquote {
  font-size: 1.8rem;
}