@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-nosotros {
    display: grid;
    gap: 2rem;

    @include m.telefono{
        grid-template-columns: repeat(2, 1fr);
    }

    @include m.desktop{
        grid-template-columns: repeat(3, 1fr);
    }

    .seccion {
        padding: 2rem;
        background-color: lighten($color: v.$morado, $amount: 30%);
        display: grid;
        gap: 2rem;

        span {
            font-size: 2.2rem;
            font-weight: 700;
            color: v.$grisOscuro;
        }
    }
}