@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {

    @include m.tablet{
        display: flex;
        height: 100vh;

        .sidebar{
            flex: 0 0 30rem;
        }

        .contenedor-principal {
            flex: 1;
            overflow: auto;
        }
    }
}

.contenedor-principal {
    margin-bottom: 3rem;

    .barra {
        padding: 1rem;
        box-shadow: 0px 5px 5px 0px v.$grisOscuro;
        @include m.tablet{
            display: flex;
            justify-content: space-between;
        }

        p {
            margin: 0;
            color: v.$grisOscuro;
            font-weight: 700;
            font-size: 2rem;
            margin-bottom: 2rem;

            @include m.tablet{
                margin-bottom: 0;
            }

            span {
                color: v.$azul;
            }
        }

        .cerrar-sesion {
            @include m.boton(v.$azul, v.$blanco);
            margin: 0;
            padding: .5rem 1rem;
            font-size: 1.6rem;
        }
    }
}