@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar {
    padding: 2rem;
    background-color: v.$azul;

    .rancho-meneses {
        font-size: 3rem;
        font-weight: 700;
        color: v.$blanco;
    }

    .navegacion-sidebar {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        a {
            padding: 1rem 2rem;
            color: v.$blanco;
            font-weight: 700;
            font-size: 1.4rem;
            border-radius: .5rem;

            &:hover {
                background-color: darken($color: v.$azul, $amount: 10%);
            }
        }
    }
}