@use 'base/variables' as v;

.alerta {
    padding: 1rem 1rem 1rem 3rem;
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: 700;

    &.exito {
        border-bottom: .3rem solid v.$verde;
        color: v.$verde;
        background-color: lighten($color: v.$verde, $amount: 40%);
    }

    &.error {
        border-bottom: .3rem solid v.$rojo;
        color: v.$rojo;
        background-color: lighten($color: v.$rojo, $amount: 40%);
    }
}