@use 'base/variables' as v;

*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-family: v.$fuentePrincipal;
    font-size: 62.5%;
}

body {
    font-size: 16px;
}

a {
    text-decoration: none;
}

h1 {
    font-size: 3.4rem;
}

h2 {
    font-size: 3rem;
}

h3 {
    font-size: 2.6rem;
}

h1, 
h2,
h3 {
    margin: 0;
}

textarea {
    resize: none;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

ul {
    padding: 0;
    margin: 0;
    list-style: none;
}