@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenido-producto {
    background-color: lighten($color: v.$naranja, $amount: 10%);
    padding: 2rem;
    
    @include m.desktop{
        display: flex;
        gap: 2rem;
    }

    .imagen {
        flex: 0 0 40rem;
    }

    .caracteristicas-producto {
        flex: 1;
        display: grid;
        gap: 2rem;
        margin-top: 3rem;

        @include m.desktop{
            margin-top: 0;
        }

        li {
            color: v.$blanco;
            font-weight: 700;
            font-size: 2rem;
        }
    }

}

button.eliminar {
    @include m.boton(v.$rojo, v.$blanco);
}

a.actualizar {
    @include m.boton(v.$azul, v.$blanco);
}

button.disponible {
    @include m.boton(v.$verde, v.$blanco);
}

button.no-disponible {
    @include m.boton(v.$morado, v.$blanco);
}