@use 'base/variables' as v;
@use 'base/mixins' as m;

.opciones-panel {
    display: grid;
    gap: 1rem;

    @include m.tablet{
        grid-template-columns: repeat(2, 1fr);
    }

    @include m.desktop {
        grid-template-columns: repeat(3, 1fr)
    }

    a,
    button {
        @include m.boton(v.$morado, v.$blanco);
    }
}