@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    max-width: 60rem;
    margin: 0 auto;

    .campo {
        display: grid;
        gap: 1rem;
        margin-bottom: 2rem;

        &:last-of-type {
            margin-bottom: 0;
        }

        label {
            display: block;
            font-size: 2rem;
            font-weight: 700;
        }

        input,
        textarea {
            width: 100%;
            margin: 0 auto;
            padding: .5rem;
            border-radius: .5rem;
            border: v.$grisClaro solid 1px;
        }
    }
    
    .imagen {
        max-width: 20rem;
    }
}