@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-galeria {
    display: grid;
    gap: 2rem;

    @include m.telefono{
        grid-template-columns: repeat(2, 1fr);
    }

    @include m.desktop{
        grid-template-columns: repeat(3, 1fr);
    }

    .imagen {
        display: grid;
        gap: 2rem;
        padding: 1rem;
        background-color: lighten($color: v.$morado, $amount: 20%);

        span {
            color: v.$blanco;
            font-size: 2rem;
            font-weight: 700;
        }
    }
}