@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenido-calaverita {
    padding: 2rem;
    background-color: lighten($color: v.$naranja, $amount: 20%);

    .calaverita {
        border: none;
        &:disabled {
            color: v.$negro;
            padding: 2rem;
            width: 100%;
            min-height: 50rem;
            line-height: 1.6;
            font-size: 2rem;
        }
    }

    .caracteristicas-calaverita {
        padding: 1rem;

        li {
            font-size: 2rem;
            font-weight: 700;
            color: v.$grisOscuro;
            padding: 1rem 0;
        }
    }
}