@use 'base/mixins' as m;
@use 'base/variables' as v;

.login {
    .titulo {
        background: linear-gradient(45deg, v.$morado, lighten(v.$morado, 30%));
        color: transparent;
        background-clip: text;
    }

    .formulario {
        label {
            color: v.$morado;
        }

        input[type="submit"] {
            @include m.boton(v.$morado, v.$blanco);
        }
    }
}