@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenido-imagen {
    padding: 2rem;
    display: grid;
    background-color: lighten($color: v.$morado, $amount: 30%);

    .descripcion {
        color: v.$grisOscuro;
        margin: 2rem 0;
    }

    .caracteristicas-imagen {
        color: v.$gris;
        font-size: 2.4rem;
        font-weight: 700;
    }
}